        //页面的滑动
        paperScroll() {
            this.scrollTop = this.$refs.scrollContent.scrollTop;
            let offsetTop = this.$refs.sideBar.offsetTop;
            var platformHeight=0;
            //安卓下添加容错高度
            if (this.$devicePlatform == 'ios') {
                var platformHeight=0;
            }else{
                var platformHeight=2;
            }
            // console.log(this.scrollTop,offsetTop-this.flexFullHeight-2)
            if (this.scrollTop >= offsetTop - this.flexFullHeight - platformHeight) {
                this.$refs.scrollContent.scrollTop = offsetTop - this.flexFullHeight;
                this.isSideBarFixed = true;
                this.isScrollWrapper = true;
                setTimeout(() => {
                    this.isScrollWrapper = false;
                }, 100)
            } else {
                this.isSideBarFixed = false;
                this.$refs.sideBar.scrollTop = 0;
            }
            console.log("===========");
            console.log(this.scrollTop, offsetTop - this.flexFullHeight - 2, this.isSideBarFixed)
        },
        //商品列表的滑动
        classifyScroll() {
            //如果是满减、打包分类，不需要加载更多数据
            if(this.leftClassFlag==0||this.leftClassFlag==1){
                return;
            }
            let scrollTop = this.$refs.sideBar.scrollTop;
            let clientHeight = this.$refs.vanList.clientHeight;

            if (clientHeight - scrollTop - this.goodsListHeight < 600 && !this.loading) {
                this.onLoadGoodsList();
            }
        },
        //普通商品列表---开始滑动
        handleTouchStart: function (e) {
            this.startPoint = e.touches[0].pageY;
        },
        handleTouchMove: function (e) {
            let poChange = e.changedTouches[0].pageY - this.startPoint;
            // this.pointChange = poChange
            // console.log(poChange)
        },
        //普通商品列表---结束滑动
        handleTouchEnd: function (e) {
            //如果是满减、打包分类，不执行上滑切换分类
            if(this.leftClassFlag == 0||this.leftClassFlag == 1){
                return
            }
            let goodsListTop = this.$refs.sideBar.scrollTop;
            let goodsBantListHeight = this.$refs.sideBar.scrollHeight;
            let end = e.changedTouches[0].pageY;
            let start = this.startPoint;
            let delta = start - end;
            if (this.isSideBarFixed) {
                console.log(goodsListTop + this.goodsListHeight + 10, goodsBantListHeight);
                if ((goodsListTop + this.goodsListHeight + 10 >= goodsBantListHeight && this.finished) || (this.goodsList.length == 0 && this.isloading)) {
                    if (delta > 150) {

                        if (this.activeKey != this.leftClassList.length - 1) {
                            this.showloading = store.get("showLoadingView");
                            if (this.showloading) {
                                this.showloading(true);
                            }
                            setTimeout(() => {
                                if (this.showloading) {
                                    this.showloading(false);
                                }
                                this.activeKey = this.activeKey + 1;
                                if (this.activeKey >= 9) {
                                    this.$refs.leftSideBar.$el.scrollTop = this.$refs.leftSideBar.$el.clientHeight;
                                }
                                this.switchLeftClass(this.activeKey);
                            }, 1000)
                        }
                    }
                }
            }


        },
