        /**
         * 获取店铺促销商品列表
         * @param
         * shopCode  Number      店铺CODE
         * areaCode  Number      小区编码
         * actType   Number      活动类型
         * ownerCode Number      业主编码
         **/
        getPromotionSkuInfo(){
            let paramData ={
                shopCode:this.shopCode,
                areaCode:this.$userInfo.AREA_CODE,
                actType:this.leftClassFlag,
                ownerCode:this.$userInfo.OWNER_CODE,
            }
            getPromotionSkuInfoService(paramData).then(res => {
                if (res.resultCode == 0) {
                    // console.log(res)
                    let rulesTxt = "";
                    res.data.map((item)=>{
                        item.sortNum = 0;
                        this.finished = true;
                        if(item.fullReductionRules){
                            item.fullReductionRules.map((rulesItem,rulesIndex)=>{
                                if(item.fullReductionRules.length == 1){
                                    rulesTxt= item.actTag;
                                    return
                                }
                                if(rulesIndex == item.fullReductionRules.length-1){
                                    rulesTxt+='满'+rulesItem.ruleFull+'减'+rulesItem.ruleReduction;
                                    return
                                }
                                rulesTxt+='满'+rulesItem.ruleFull+'减'+rulesItem.ruleReduction+'，';
                            })
                            item.rulesTxt = rulesTxt;
                            rulesTxt = "";
                        }
                    })
                    this.promotionGoodsList = res.data;
                    this.$nextTick(()=>{
                        this.$refs.reducedGoodsItem.map((item)=>{
                            this.reducedGoodsOffSetTopList.push(item.offsetTop-this.$refs.normalTitle.clientHeight)
                        })
                        console.log(this.reducedGoodsOffSetTopList)
                    })
                    this.isShowBlankImg = false;
                }
            }).catch(error => {
            });
        },
        getNewPromotionGoodsList(){
            let paramData ={
                shopCode:this.shopCode,
                areaCode:this.$userInfo.AREA_CODE,
                actType:this.leftClassFlag,
                ownerCode:this.$userInfo.OWNER_CODE,
            }
            getPromotionSkuInfoService(paramData).then(res => {
                if (res.resultCode == 0) {
                    this.promotionGoodsList.map((item)=>{
                        item.skus.map((goodsItem)=>{
                            res.data.map((resItem)=>{
                                resItem.skus.map((resGoodsItem)=>{
                                    if(goodsItem.skuNo == resGoodsItem.skuNo){
                                        goodsItem.storeNumber = resGoodsItem.storeNumber;
                                        goodsItem.priceLow = resGoodsItem.priceLow;
                                        goodsItem.cartCount = resGoodsItem.cartCount;
                                        goodsItem.priceLine = resGoodsItem.priceLine;
                                    }
                                })
                            })
                        })
                    })
                }
            }).catch(error => {
            });
        },
